import "../styles/fleetStatus.scss";
import carIcon from "../assets/car.png";
import { useState } from "react";
import { useContext } from "react";
import { LocationContext, CordType, DriverProps } from "../contexts/Location";

export function FleetStatus() {
  const [currentTab, setCurrentTab] = useState(0);
  const { cords, drivers } = useContext(LocationContext);
  return (
    <div id="fleet-status-container">
      <div id="status-menu">
        <div
          onClick={() => setCurrentTab(0)}
          className={`status-menu-item ${currentTab === 0 ? "clicked" : ""}`}
        >
          <h3>Veículos</h3>
        </div>

        <div
          onClick={() => setCurrentTab(1)}
          className={`status-menu-item ${currentTab === 1 ? "clicked" : ""}`}
        >
          <h3>Mensagens</h3>
        </div>
      </div>
      <div id="content">
        {currentTab === 0 ? <VehiclesList content={drivers} /> : <MsgList />}
      </div>
    </div>
  );
}

function MsgList() {
  return (
    <ul id="msg-list">
      <li>
        <img
          src="https://lh3.googleusercontent.com/a-/AOh14GiwlWQKHCsOvn5sAP98V_tADQHNMiGZe9j_D5pUCw=s96-c"
          alt="profile"
        />
        <div id="msg-info1">
          <h3>Bruno Good</h3>
          <p>Boa tarde</p>
        </div>
        <div id="msg-info2">
          <p>14:30</p>
          <div id="msg-ball"></div>
        </div>
      </li>
    </ul>
  );
}

type Params = {
  content: DriverProps[];
};

function getDate(timestamp: string) {
  const value = new Date(parseInt(timestamp) * 1000);

  return `${value.getDay()}/${
    value.getMonth() + 1
  }/${value.getFullYear()} - ${value.getHours()}h`;
}

function VehiclesList({ content }: Params) {
  const { setCordsOnFocus } = useContext(LocationContext);
  return (
    <ul id="vehicles-list">
      {content.map((i) => {
        return (
          <li onClick={() => setCordsOnFocus(i.driverId)} key={i.driverId}>
            <img src={carIcon} alt="carIcon" />
            <div className="text">
              <h4>{i.driverName}</h4>
              <p>{`Última atualização:`}</p>
            </div>
            <strong>90km/h</strong>
            <div className="status-ball"></div>
          </li>
        );
      })}
    </ul>
  );
}
