import firebase from "firebase";
import { useEffect } from "react";
import { ReactNode } from "react";
import { useState } from "react";
import { createContext } from "react";
import { auth } from "../services/firebase";
import { useHistory } from "react-router-dom";
import api from "../services/api";
import axios from "axios";
import toast, { Toaster } from "react-hot-toast";

type ComponentProps = {
  children: ReactNode;
};

type AuthObject = {
  isAuthenticated: boolean;
  signInWithGoogle: () => void;
  signOut: () => void;
  user: firebase.User;
};

export const AuthContext = createContext({} as AuthObject);

export function AuthContextProvider(props: ComponentProps) {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [user, setUser] = useState<firebase.User>({} as firebase.User);
  const history = useHistory();

  useEffect(() => {
    const unsuscribe = auth.onAuthStateChanged((_user) => {
      if (_user) {
        setIsAuthenticated(true);
        setUser(_user);
      }
    });

    return () => {
      unsuscribe();
    };
  });

  function signInWithGoogle() {
    var provider = new firebase.auth.GoogleAuthProvider();
    firebase
      .auth()
      .signInWithPopup(provider)
      .then((result) => {
        /** @type {firebase.auth.OAuthCredential} */
        var credential = result.credential;
        var user = result.user;

        api
          .post("driver", {
            driverId: user?.uid,
            driverName: user?.displayName,
            driverPicture: user?.photoURL,
          })
          .then((r) => {
            toast.success("Login realizado com sucesso");
            console.log(r);
            setIsAuthenticated(true);
            history.push("/");
          })
          .catch((err) => {
            toast.success("Erro no login, tente novamente");
            console.log(err);
          });
      })
      .catch((error) => {
        var errorCode = error.code;
        var errorMessage = error.message;
        var email = error.email;
        var credential = error.credential;
      });
  }

  async function signOut() {
    await firebase.auth().signOut();
    setIsAuthenticated(false);
    toast.success("Logout realizado");
    history.push("/");
  }

  return (
    <AuthContext.Provider
      value={{ user, isAuthenticated, signInWithGoogle, signOut }}
    >
      <Toaster />
      {props.children}
    </AuthContext.Provider>
  );
}
