import { NavBar } from "./NavBar";
import "../styles/global.scss";
import "../styles/layoutScreen.scss";
import { Map } from "./Map";
import { FleetStatus } from "./FleetStatus";
import { useContext } from "react";
import { LayoutContext, Layout as LayoutProps } from "../contexts/Layout";
import { CheckLists } from "./CheckLists";
import { Drivers } from "./Drivers";
import { Estatistics } from "./Estatistics";
import { Filters } from "./Filters";
import { Finances } from "./Finances";
import { Fleet } from "./Fleet";
import { Routes } from "./Routes";
import { Settings } from "./Settings";
import { Calls } from "./Calls";
import { ProfileBall } from "./ProfileBall";

function getCurrentLayout(param: LayoutProps) {
  switch (param) {
    case "calls":
      return <Calls />;
    case "checks":
      return <CheckLists />;
    case "drivers":
      return <Drivers />;
    case "estatistics":
      return <Estatistics />;
    case "filters":
      return <Filters />;
    case "finances":
      return <Finances />;
    case "fleet":
      return <Fleet />;
    case "map":
      return <Map />;
    case "routes":
      return <Routes />;
    case "settings":
      return <Settings />;
  }
}

export function Layout() {
  const { currentLayout } = useContext(LayoutContext);
  return (
    <>
      <div id="layout-screen-container">
        <NavBar />
        <ProfileBall />
        {getCurrentLayout(currentLayout)}
        <FleetStatus />
      </div>
    </>
  );
}
